<template lang="html">
  <div class="wrap">
      <div class="header"><h1 slot="title">商品分类</h1></div>
      
    <section class="view">
      <v-aside :datas="allData"></v-aside>
      <router-view :datas="allData"></router-view>
    </section>
 <v-footer></v-footer>
  </div>
</template>

<script>
import Aside from "@/components/category/aside.vue";
import data from "@/assets/data.js";
import Footer from "@/common/_footer.vue";
import category from "@/http/mock.js"; //模拟数据
export default {
  components: {
    "v-aside": Aside,
    "v-footer": Footer
  },
  data() {
    return {
      allData: ""
    };
  },
  created() {
    this.allData = data.allList;
  }
};
</script>

<style lang="less" scoped>
.header {
  background-color: #f8fcff;
  text-align: center;
  height: 12vw;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 4vw;
}
.wrap {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-flow: column nowrap;
  flex-flow: column nowrap;

  .view {
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
}
</style>



