<template lang="html">
  <!-- gotoRouter:点击后路由跳转到与id名相同的route.name所对应的路径 -->
  <footer class="footer" @click.stop='gotoRouter'>
    <mt-tabbar v-model="selected" fixed>
    <mt-tab-item id="首页" class="foot_item">
      <img slot="icon" src="../assets/icon/17home.png" >首页</img>     
    </mt-tab-item>
    <mt-tab-item id="分类页">
      <img slot="icon" src="../assets/icon/分类.png" >分类</img>  
    </mt-tab-item>
    <mt-tab-item id="购物车页">
      <img slot="icon" src="../assets/icon/46trolley.png" >购物车</img>
    </mt-tab-item>
    <mt-tab-item id="用户页">
      <img slot="icon" src="../assets/icon/我的.png" >我的</img>
      
    </mt-tab-item>
  </mt-tabbar>
  </footer>
</template>

<script>
export default {
  methods: {
    gotoRouter() {
      this.$router.push({
        name: this.selected
      });
    }
  },
  data() {
    return {
      //对应mt-tab-item 的id值
      selected: ""
    };
  },
  mounted() {
    //获取当前路由名称，根据该名称给当前footer添加is-selected
    let Rname = this.$route.name;
    switch (Rname) {
      case "首页":
        this.selected = "首页";
        break;
      case "分类页":
        this.selected = "分类页";

        break;
      case "购物车页":
        this.selected = "购物车页";

        break;
      case "用户页":
        this.selected = "用户页";
        break;
    }
  }
};
</script>

<style lang="less" scoped>
@import "../assets/fz.less";
@import "../assets/index/style.css";

.mint-tab-item-label:hover {
  color: #333;
}
.footer {
  .mint-tabbar {
    background-color: #fff;
    background-image: none;
    box-shadow: 0 0 2.2vw 0 hsla(0, 6%, 50%, 0.13);
    -webkit-box-shadow: 0 0 2.2vw 0 hsla(0, 6%, 50%, 0.13);
    .is-selected {
      color: @cl;
      background-color: #fff;
      i {
        &::before {
          color: @cl;
        }
      }
    }
    i {
      .fz(font-size, 42);
    }
  }
}
.foot_item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
</style>



